$main-font: "Open-sans", sans-serif;
$bolt-font: "Open-sans-bold", sans-serif;

@font-face {
  font-family: "Open-sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff") format("woff");
}

@font-face {
  font-family: "Open-sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2");
}

@font-face {
  font-family: "Open-sans-bold";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensansbold.woff") format("woff");
}

@font-face {
  font-family: "Open-sans-bold";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2");
}
